.welfare2{
	margin-top:90px;
	
	.welfare-item{
		ul{
			width:100%;
			height:auto;
			li{
				width:100%;
				height:auto;
				position:relative;
				img{
					width:100%;
					font-size: 0;
				}
				.welfare-content{
					position:absolute;
					top:0;
					left:0;
					width:100%;
					height:100%;
					padding:$f10;
					.items{
						width:100%;
						height:48%;

						div{
							-webkit-flex:1;
							flex:1;
							// padding-left:$f10;
						}
						.items-money{

							color:$red;	
							i{
								font-size:$f15;
								display: inline-block;
								vertical-align: top;
								margin-top:0.5rem;
							}
							span{
								font-size: 3rem;
							}
							border-right:1px solid #ccc;
							padding-right:$f10;
						}

						.condition{
							div{
								// margin:0 auto;
								text-align:right;
								font-size: $f12;
								color:#717171;
								line-height: $f15;
								span{
									display: inline-block;
								}
							}
						}

						.item-from{
							// text-align: center;
							padding-right:$f10;
							font-size:$f10;
							color:#717171;
							.item-name{
								width:100%;
							}
							border-right:1px solid #ccc;
						}

						.item-duration{
							
							font-size:$f11;
							color:#717171;

							div{
								width:100%;
								text-align:right;
							}
						}

					}
				}
			}
		}
	}

}