.mission-center{
	margin-top:40px;
	.mission-progress{
		font-size: $f12;
		color:#666666;
		padding-top:0.5rem;
		padding-bottom:0.5rem;
		span{
			color:$red;
		}
	}
	.missions{
		.mission-title{
			font-size: $f14;
			color:#666;
			background-color:#f2f2f2;
			padding-top:0.5rem;
			padding-bottom:0.5rem;
		}
		.mission-list{
			ul{
				width:100%;
				height:auto;
				li{
					display: block;
					width:100%;
					border-bottom:1px solid #f2f2f2;
					padding-top:$f10;
					padding-bottom:$f10;
					.mission-items{
						height:3.5rem;
						line-height:3.5rem;
						font-size:$f13;
						color:#4c4c4c;
						.mission-icon{
							border:1px solid $orange;
							border-radius:100%;
							width:3.5rem;
							height:3.5rem;
							text-align: center;
							line-height: 3.5rem;
							color:$orange;
							margin-right:$f10;
							.iconfont{
								font-size: $f18;
							}
							.icon-touzi1,.icon-yiyuebiao,.icon-sanyuebiao,.icon-liuyuebiao{
								font-size:2.2rem;
							}
						}
						.mission-points{
							color:$red;
						}
					}

					
				}
			}

		}
	}
}

