.setup{
	margin-top:40px;
	ul{
		height:auto;
		background-color:$white;
		li{
			font-size:$f13;
			color:#2d2d2d;
			border-bottom:1px solid #d6d6d6;
			height:4rem;
			line-height: 4rem;

			.iconfont{
				font-size:$f18;
				margin-right:$f10;
				color:$red;
			}
			.toNext{
				line-height: 4rem;
				.iconfont{
					font-size:$f13;
					margin-right:0;
					color:#d6d6d6;
					display:inline-block;
  					vertical-align:middle; 
				}
			}

			.icon-shenfen{
				display: inline-block;
				vertical-align: top;
				margin-top: 0.1rem;
			}
			.icon-353245214507{
				font-size: $f20;
				display: inline-block;
				vertical-align: top;
				// margin-top:0.2rem;
			}
			.icon-zhifu{
				display: inline-block;
				vertical-align: middle;
				// top:0.2rem;
			}

		}
		.right-words{
			color:#6d6d6d;
			font-size: $f11;
		}
	}
}